.popular-location {
  border: 2px solid black;
  width: 100%;
  margin: 0 1rem 1rem 0;
  border-radius: 10px;
  padding: 1rem 0 1rem 1rem;
  /* seemore button click animation */
  opacity: 0;
  animation: fadeIn 0.5s ease forwards;
}
/* see more button click animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.popular-location .location-wrapper {
  display: flex;
  justify-content: space-between;
}
.location-wrapper .location-icon {
  color: black;
  font-size: 2rem;
  display: flex;
  align-items: center;
}
.location-text h3 {
  font-size: 1.7rem;
  font-weight: 300;
}
.location-text p {
  font-size: 1.3rem;
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .location-text h3 {
    font-size: 1.4rem;
    font-weight: 300;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .location-text h3 {
    font-size: 1.35rem;
  }
  .location-text p {
    font-size: 1.2rem;
  }
}
