.herofaq-wrapper {
  width: 80%;
  border: 1px solid black;
  margin: 2rem auto;
  border-radius: 5px;
}
.herofaq-wrapper .question-wrapper,
.herofaq-answer {
  display: flex;
  justify-content: space-between;
  text-align: justify;
  font-size: 2rem;
  padding: 2rem 2rem;
}
.herofaq-question {
  margin-right: 2rem;
}

.plus-icon {
  font-size: 2rem;
  transition: transform 0.2s ease;
}
.open .plus-icon {
  transform: rotate(45deg);
}
.herofaq-wrapper .herofaq-answer {
  display: none;
  overflow: hidden;
}
.open .herofaq-answer {
  display: block;
}

@media only screen and (max-width: 767px) {
  .herofaq-wrapper .question-wrapper,
  .herofaq-answer {
    font-size: 1.8rem;
  }
}
